.banner-slider {
  height: calc(100vh - 90px);
  .slick-dotted.slick-slider {
    margin-bottom: 0px; 
  }
  .slick-list {
    margin: 0 0 !important;
  }
  .slick-slide {
    margin: 0 0 !important;
  }
  .slick-dots {
    bottom: 36px;

    li button:before {
      font-size: 10px;
    }
  }

  .arrow-right {
    display: inline-block;
    position: absolute;
    right: 0;
    margin-right: 20px;
    z-index: 10;
    top: 50%;
  }
  .arrow-left {
    display: inline-block;
    position: absolute;
    left: 0;
    margin-left: 20px;
    z-index: 10;
    top: 50%;
  }
  .arrow-left, .arrow-right {
    svg {
      height: 25px;
      width: 25px;
    }
  }
}
.hero-banner {
  background: url(../../assets/images/banner.webp);
  display: inline-block;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  width: 100%;
  height: 100%;
  height: 100vh;
}
.hero-banner1.hero-banner { 
    background: url(../../assets/images/volunteer.webp) !important;
}
.hero-banner .container {
  height: 100%;
}
.banner-text-box {
  display: flex;
  justify-content: flex-start;
  height: 100%;
  align-items: center;
  flex-direction: row;
  width: 84%;
}
.banner-text-info {
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  width: 28%;
  margin-top: 19%;
  margin-left: 1%;
  padding: 8%;

  h4 {
    padding-bottom: 5px;
  }
  p {
  font-size: 16px;
  }
}
.banner-inner-text {
  line-height: 32px;
  width: 60%;

  .get-started {
    width: 27%;
    margin-top: 30px;
  }
}
.banner-inner-text p {
  padding-bottom: 20px;
}